const osName = platform();

const Persik = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Настройки
		</PanelHeader>
		<iframe align="absmiddle" title="video1" allowFullScreen id="vd2" frameBorder="0" allow="geolocation; microphone; camera" width="100%" height="480" src={sr2}></iframe>
	</Panel>
);

Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Persik;
